import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanRates } from './bean/interest-rates.bean';
import { ConfirmationService } from 'primeng/primeng';
// import { IsPool, IsSource, fromSys } from './../constant/codeValue';
// import { IsVariousClass } from './../constant/codeValue';
// import { IsRcord } from './../constant/codeValue';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-interest-rates',
  templateUrl: './interest-rates.component.html',
  styleUrls: ['./interest-rates.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestRates implements OnInit {
  beanRates: BeanRates = new BeanRates();
  //维度所属分池
  dmsnlPoolList: any = [];
  //所属分类
  dmsnlClassList: any = [];
  //状态
  rcrdStCodeList: any = [];
  //table数据
  list: any = "";
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  data1: any = [];
  data2: any = [];
  first: number = 0;//分页控制
  isVariousClass: any = [];
  isRcord: any = [];
  isSource: any = [];
  fromSys: any = [];
  isPool: any = [];
  code: any;//获取所有码值
  total: number;
  tabMesg: any;//表格中无数据时显示的内容
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc) {
    this.codeValues();
    this.isPool = this.code['IsPool'];
    this.isVariousClass = this.code['IsVariousClass'];
    this.isSource = this.code['IsSource'];
    this.isRcord = this.code['IsRcord'];
    this.fromSys = this.code['fromSys'];
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.beanRates.pageSize = 10;
    this.beanRates.pageNum = 1;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.beanRates.pageSize = 10;
    this.beanRates.pageNum = 1;
    this.first = 0;
    this.query();
  }
  //查询
  query() {
    this.httpService.queryDimensionMaintService(this.beanRates).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      if (data.resultList) {
        this.list = data.resultList
      }
      this.total = data.total;
    }, err => {
      this.tabMesg = tableMessage
    })
  }

  //重置
  newSet() {
    this.beanRates = new BeanRates();
    this.beanRates.pageNum = 1;
    this.beanRates.pageSize = 10;
    //  this.first=0; 
  }
  //添加
  add(param) {
    this.headerTitle = '计价维度新增';
    // this.display = true;
    // this.showModel = '2';
    this.router.navigate(['/pages/tzb/valuation/interest-rates/add'])

  }
  //新增的回调函数
  addCall(param) {
    // alert(1);
    this.display = param;
  }
  //修改的回掉函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.query();
  }
  //详情
  detail(param) {
    this.headerTitle = '计价维度-详细信息';
    this.showModel = '3';
    this.display = true;
    this.inValueCode = param.dmsnlCode;
  }

  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }

  //更新
  update(param) {
    this.headerTitle = '计价维度-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param.dmsnlCode;

  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.beanRates.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanRates.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanRates.pageSize;
    //调用查询的方法
    this.query();
  }

  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          dmsnlCode: car.dmsnlCode,
        };
        this.httpService.deleteDimensionMaintService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
      }
    });
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
