import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { BeanRates } from './../bean/interest-rates.bean';
// import { IsPool } from './../../constant/codeValue';
// import { IsVariousClass } from './../../constant/codeValue';
// import { IsSource } from './../../constant/codeValue';
// import { IsModel, strType, strType1, fromSys } from './../../constant/codeValue';
import { Message } from 'primeng/primeng';
import { CheckboxModule } from 'primeng/primeng';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { specialSymbol, checkEn } from 'app/@uisftech/common/service/validator';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss'],
  providers: [Commfunc]
})
export class AddComponent implements OnInit, OnChanges {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  isPool: any = [];
  // isVarious: any = [];
  // isVarious2: any = IsVarious;
  IsVariousClass: any = [];
  isSource: any = [];
  isModel: any = [];
  strType: any = [];
  strType1: any = [];
  fromSys: any = [];
  code: any;//获取所有码值
  isRed: boolean = false;
  isRed2: boolean = false;
  isRed3: boolean = false;
  show: boolean = false;
  beanRates: BeanRates = new BeanRates();
  userform: FormGroup; //校验  
  taskCategoryId: string;
  isDisable: boolean = false;
  stringType: boolean = false;
  dmsnlReflectShow: boolean = false;
  //提示信息
  msgs: Message[] = [];
  dmslModule: Array<any> = [];
  constructor(
    public httpService: ValuationHttpService,
    public fb: FormBuilder,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isPool = this.code['IsPool'];
    this.IsVariousClass = this.code['IsVariousClass'];
    this.isSource = this.code['IsSource'];
    this.isModel = this.code['IsModel'];
    this.strType = this.code['strType'];
    this.strType1 = this.code['strType1'];
    this.fromSys = this.code['fromSys'];

    //   //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }
  ngOnChanges() {
    this.beanRates = new BeanRates();
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.userform = this.fb.group({
      'dmsnlChiNm': new FormControl('', [Validators.required, Validators.maxLength(60), specialSymbol]),
      'englishName': new FormControl('', [Validators.required, Validators.maxLength(60), checkEn]),
      'dmsnlDesc': new FormControl('', [Validators.required, Validators.maxLength(255), specialSymbol]),
      'dmsnlPool': new FormControl('', Validators.required),
      'dmsnlClass': new FormControl('', Validators.required),
      'detSrcFlag': new FormControl('', Validators.required),
      'dmslModule': new FormControl('', Validators.required),
      'detSrcSys': new FormControl('', specialSymbol),
      'detSrcMethod': new FormControl('', [Validators.nullValidator, Validators.maxLength(225), specialSymbol]),
      'detSreMetDesc': new FormControl('', [Validators.nullValidator, Validators.maxLength(100), specialSymbol]),
      'dmsnlCharType': new FormControl('', Validators.required),
      'dmsnlReflect': new FormControl('', Validators.maxLength(60))
    });
    this.beanRates.detSrcFlag = '';

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //做置灰
  disableText() {
    if (this.beanRates.detSrcFlag == "1") {
      this.beanRates.detSreMetDesc = null;
      this.beanRates.detSrcSys = null;
      this.beanRates.detSrcMethod = null;
      this.beanRates.dmsnlReflect = null;
      this.show = true;
      this.isRed = false;
      this.isRed2 = false;
      this.isRed3 = false;
      this.dmsnlReflectShow = true;
    } else {
      this.isRed = true;
      this.isRed2 = true;
      this.isRed3 = true;
      this.show = false;
      this.dmsnlReflectShow = false;
    }

    // if (this.beanRates.detSrcFlag == "1" || this.beanRates.detSrcFlag == "3") {
    //   this.stringType = true
    // } else {
    //   this.stringType = false
    // }
  };
  //保存
  add() {
    if (this.isDisable) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    };

    //检验维度映射字段
    if ((this.beanRates.detSrcFlag == '2' || this.beanRates.detSrcFlag == '3') && !this.beanRates.dmsnlReflect) {
      return
    }
    //维度英文名称长度
    if (this.beanRates['englishName'].length > 60) {
      return
    }
    //取数来源外系统
    if ((this.beanRates.detSrcFlag == '2' || this.beanRates.detSrcFlag == '3') && !this.beanRates.detSrcSys) {
      return
    }
    this.beanRates.detSrcSys = this.beanRates.detSrcSys || '';
    this.beanRates.detSrcMethod = this.beanRates.detSrcMethod || '';
    this.beanRates.detSreMetDesc = this.beanRates.detSreMetDesc || '';
    if (this.beanRates.detSrcFlag != '1') {
      if (!this.beanRates.detSrcSys || !this.beanRates.detSrcMethod || !this.beanRates.detSreMetDesc) {
        this.show = false;
        this.isRed = true;
        this.isRed2 = true;
        this.isRed3 = true;
        return
      }
    }
    this.outValue.emit(false);
    if (this.dmslModule) {
      this.beanRates.dmslModule = this.dmslModule.toString()
    }
    //处理工作流的参数
    this.beanRates['taskCategoryId'] = this.taskCategoryId + '';
    this.isDisable = true;
    //提交服务

    this.httpService.addDimensionMaintService(this.beanRates).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.beanRates = new BeanRates();
        // this.outValue.emit(data.returnCode.message);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 1000)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //取消
  cancel() {
    // this.router.navigate(['/pages/tzb/valuation/interest-rates'])
    window.history.go(-1);
  }

  //维度所属分池控制维度所属分类的下拉框
  // doChange() {

  //   if (this.beanRates.dmsnlPool == '1') {
  //     this.isVarious = this.isVarious2.slice(0, 6);
  //   }
  //   if (this.beanRates.dmsnlPool == '2') {
  //     this.isVarious = this.isVarious2.slice(6, 8);
  //     this.isVarious.unshift({ label: '请选择', value: "" });
  //   }
  //   if (this.beanRates.dmsnlPool == '3') {
  //     this.isVarious = this.isVarious2.slice(8, 10);
  //     this.isVarious.unshift({ label: '请选择', value: "" });
  //   }
  // }
}